import { Graphics, Texture } from "pixi.js";
import { Card } from "../../core/Card";
import { CardGridComponent } from "./CardGridComponent";
import { Component } from "./pixi/Component";
import { Cell } from "./pixi/Cell";
import { EaseFunc } from "./pixi/Ease";
import { Color } from "./pixi/Color";
import { GridComponent } from "./GridComponent";

interface ICardPreviewComponentProps {
  card: Card;
  spFire: boolean;
}

export class CardPreviewComponent extends Component<ICardPreviewComponentProps> {
  private readonly bg = new Graphics();

  layout = {
    width: 153,
    height: 196,
    radius: 7,
    padding: 9,
    boardScale: 0.934,
    bgColor: 0x4f5055,
    szMeter: {
      margin: 6,
      radius: 11,
      width: 48,
      height: 44,
    },
    spMeter: {
      x: 58,
      y: 153,
      width: 13,
      padding: 1.5,
    },
  };

  private readonly states = {
    angle: Cell.of(0),
    overlayAlpha: Cell.of(0),
  };

  constructor() {
    super({
      card: Card.fromId(1),
      spFire: false,
    });

    const {
      width,
      height,
      radius,
      padding,
      boardScale,
      bgColor,
      spMeter: spMeterLayout,
      szMeter: szMeterLayout,
    } = this.layout;

    this.bg = this.addGraphics()
      .beginFill(bgColor)
      .drawRoundedRect(0, 0, width, height, radius);
    this.bg.interactive = true;
    this.castShadow(this.bg, { x: 1, y: 1, alpha: 0.3 });

    const img = this.addSprite({
      x: 0,
      y: padding,
      width,
      height,
      tint: Color.fromHex(0xafafaf),
    });

    const overlay = this.addGraphics()
      .beginFill(Color.WHITE.i32)
      .drawRoundedRect(0, 0, width, height, radius);
    overlay.alpha = 0;

    const grid = this.addComponent(new CardGridComponent(), {
      x: padding,
      y: padding,
      scale: { width: width - 2 * padding, height: width - 2 * padding },
    });
    grid.update({ flat: true });
    grid.scale.y = boardScale;

    this.addGraphics()
      .beginFill(Color.BLACK.i32)
      .drawRoundedRect(
        szMeterLayout.margin,
        height - szMeterLayout.margin - szMeterLayout.height,
        szMeterLayout.width,
        szMeterLayout.height,
        szMeterLayout.radius
      );

    const szMeter = this.addText({
      anchor: 0.5,
      x: szMeterLayout.margin + szMeterLayout.width / 2,
      y: height - szMeterLayout.margin - szMeterLayout.height / 2,
      style: {
        fill: 0xeeeeee,
        fontFamily: "Splatoon",
      },
    });

    const spMeterRoot = this.addContainer({
      x: spMeterLayout.x,
      y: spMeterLayout.y,
    });
    const sxy = spMeterLayout.width / 40;
    spMeterRoot.scale.set(sxy);
    const dx = 40 + spMeterLayout.padding / sxy;
    const spMeter = this.addComponent(new GridComponent(), {
      parent: spMeterRoot,
    });
    spMeter.update({
      tileset: new Map([
        [true, Texture.from("assets/player1_special_space.png")],
      ]),
      transform: {
        // anchor: 0.5,
        dx,
        dy: dx,
      },
    });

    this.states.angle.onEase(0.2, EaseFunc.LINEAR).onUpdate((v) => {
      this.angle = v;
    });
    this.states.overlayAlpha.onEase(0.2, EaseFunc.LINEAR).onUpdate((v) => {
      overlay.alpha = v;
    });
    this.on("pointerover", () => {
      this.states.angle.update(-2);
      this.states.overlayAlpha.update(0.35);
    });
    this.on("pointerout", () => {
      this.states.angle.update(0);
      this.states.overlayAlpha.update(0);
    });

    const fn = () => {
      const card = this.props.card.value;
      const spFire = this.props.spFire.value;
      img.texture = Texture.from(`assets/cards/${card.meta.name}.png`);
      grid.update({ card });
      szMeter.text = `${card.meta.spaceCount}`;
      spMeter.update({
        matrix: {
          spaces: Array(card.meta.specialCost).fill(true),
          width: 5,
          height: 2,
        },
      });
      // TODO: support spFire
      // this.spMeter.forEach((space, i) => {
      //   const v = i < card.meta.specialCost ? Spaces.SPECIAL : Spaces.INVALID;
      //   space.update({
      //     value: v,
      //     spFire: spFire && Math.abs(v) != Spaces.INVALID,
      //   });
      // });
    };
    this.props.card.onUpdate(fn);
    this.props.spFire.onUpdate(fn);
  }

  on(event: string, callback: (event: any) => void) {
    this.bg.on(event, callback);
  }
}
