import { EaseFunc } from "./Ease";

export class Cell<T> {
  private mUpdateFunc: (v: T) => void = () => {};
  private mEaseSec = 0;
  private mEaseFunc: EaseFunc = EaseFunc.LINEAR;
  private mEaseState = { cancel: false };

  private constructor(public value: T) {}

  onUpdate(func: (v: T) => void): this {
    this.mUpdateFunc = func;
    return this;
  }

  onEase(sec: number, func: EaseFunc = EaseFunc.LINEAR): this {
    this.mEaseSec = sec;
    this.mEaseFunc = func;
    return this;
  }

  update(v1: T): Promise<void> {
    this.mEaseState.cancel = true;
    this.mEaseState = { cancel: false };

    let resolve: () => void;
    const p = new Promise<void>((_resolve) => {
      resolve = _resolve;
    });

    let t0: number = null;
    const { mEaseSec, mEaseFunc, mUpdateFunc, mEaseState, value: v0 } = this;
    const fn = (t: number) => {
      let nextFn: any = () => resolve();
      if (!mEaseState.cancel) {
        if (t0 == null) {
          t0 = t;
        }
        t = (t - t0) / (1000 * mEaseSec);
        if (isNaN(t)) {
          this.value = v1;
        } else if (t >= 1) {
          this.value = mEaseFunc.interpolate(v0, v1, 1);
        } else {
          this.value = mEaseFunc.interpolate(v0, v1, t);
          nextFn = fn;
        }
      }
      mUpdateFunc(this.value);
      window.requestAnimationFrame(nextFn);
    };
    fn(window.performance.now());

    return p;
  }

  static of<U>(value: U): Cell<U> {
    return new Cell(value);
  }
}
