import { Texture } from "pixi.js";
import { RenderBuffer } from "./pixi/RenderBuffer";

export class EmptySpaceTexture extends RenderBuffer {
  layout = {
    width: 40,
    height: 40,
    border: 1.5,
  };

  constructor() {
    super({}, false);

    const { width, height, border } = this.layout;
    const w = width - 2 * border;
    this.addGraphics()
      .beginFill(0x4b485b)
      .drawRect(0, 0, width, height)
      .beginFill(0x0a0a0a)
      .drawRect(border, border, w, w);
  }

  private static v: Texture;
  public static get V() {
    return this.v || (this.v = new EmptySpaceTexture().texture);
  }

  private static v52: Texture;
  public static get V52() {
    if (this.v52 == null) {
      const s = new EmptySpaceTexture();
      s.ui.alpha = 0.52;
      this.v52 = s.texture;
    }
    return this.v52;
  }
}
