import { Card } from "../../core/Card";
import { CardPreviewComponent } from "./CardPreviewComponent";
import { Color } from "./pixi/Color";
import { Component } from "./pixi/Component";
import { EaseFunc } from "./pixi/Ease";

interface IHandComponentProps {
  cards: Card[];
}

type DrawCardFn = (card: Card, i: number) => Promise<void>;

export class HandComponent extends Component<IHandComponentProps> {
  private hoverCardId: number = -1;
  private selectCardId: number = -1;
  private selectCardCallBack: any;

  layout = {
    width: 358,
    height: 436,
    radius: 10,
    card: {
      width: 153,
      height: 196,
    },
  };

  private drawCardFn: DrawCardFn;

  constructor() {
    super({
      cards: null,
    });

    const rect = this.addGraphics()
      .beginFill(Color.BLACK.i32)
      .drawRoundedRect(
        0,
        0,
        this.layout.width,
        this.layout.height,
        this.layout.radius
      );
    rect.alpha = 0.18;

    const paddingX = (this.layout.width - 2 * this.layout.card.width) / 3;
    const paddingY = (this.layout.height - 2 * this.layout.card.height) / 3;
    const cards: CardPreviewComponent[] = [];
    for (let y = 0; y < 2; ++y) {
      for (let x = 0; x < 2; ++x) {
        const idx = x + y * 2;
        const cardRoot = this.addContainer({
          x: (x + 0.5) * this.layout.card.width + (x + 1) * paddingX,
          y: (y + 0.5) * this.layout.card.height + (y + 1) * paddingY,
        });
        const card = this.addComponent(new CardPreviewComponent(), {
          parent: cardRoot,
          anchor: 0.5,
          scale: {
            width: this.layout.card.width,
            height: this.layout.card.height,
          },
        });
        // card.update({ spFire: true });
        card.on("pointerover", (event) => {
          this.onPointerOver(idx);
        });
        card.on("pointerout", (event) => {
          this.onPointerOut(idx);
        });
        card.on("pointertap", (event) => {
          this.onPointerTap(idx);
        });
        cards.push(card);
      }
    }

    this.onSelectCard();

    this.props.cards.onUpdate((v) => {
      if (v != null) {
        console.assert(v.length == 4);
        v.forEach((card, i) => cards[i].update({ card }));
      }
    });

    this.drawCardFn = async (v: Card, i: number) => {
      console.assert(0 <= i && i < 4);

      const li = this.props.cards.value.slice();
      li[i] = v;

      const card = cards[i];
      const a1 = this.addAnimation((t) => {
        const e = EaseFunc.EASE_IN_CUBIC.apply(t);
        card.ui.alpha = 1 - e;
        card.ui.scale.set(EaseFunc.LINEAR.interpolate(1, 0.9, e));
        card.position.y = 0;
      });

      const a2 = this.addAnimation((t) => {
        const e = EaseFunc.EASE_OUT_CUBIC.apply(t);
        card.ui.alpha = e;
        card.ui.scale.set(1);
        card.position.y = EaseFunc.LINEAR.interpolate(10, 0, e);
      });

      const dt = 0.3;
      await a1.play(dt);
      this.update({ cards: li });
      await a2.play(dt);
    };
  }

  async uiDrawCard(card: Card, handId: number) {
    await this.drawCardFn(card, handId);
  }

  uiResetSelection() {
    this.uiUpdateHoverCardId(-1);
    this.uiUpdateSelectCardId(-1);
  }

  onSelectCard(callback?: (cardId: number) => void) {
    this.selectCardCallBack = callback || (() => {});
  }

  private onPointerOver(idx: number) {
    this.uiUpdateHoverCardId(idx);
  }

  private onPointerOut(idx: number) {
    this.uiUpdateHoverCardId(-1);
  }

  private onPointerTap(idx: number) {
    this.uiUpdateSelectCardId(idx);
    this.selectCardCallBack(idx);
  }

  private uiUpdateHoverCardId(idx: number) {
    if (this.hoverCardId == idx) {
      return;
    }
    // if (this.hoverCardId >= 0 && this.hoverCardId != this.selectCardId) {
    //   this.cards[this.hoverCardId].uiUpdateHover(false);
    // }
    // if (idx >= 0) {
    //   this.cards[idx].uiUpdateHover(true);
    // }
    this.hoverCardId = idx;
  }

  private uiUpdateSelectCardId(idx: number) {
    if (this.selectCardId == idx) {
      return;
    }
    // if (this.selectCardId >= 0) {
    //   const card = this.cards[this.selectCardId];
    //   card.angle = 0;
    //   card.scale.set(1);
    //   card.uiUpdateHover(false);
    // }
    // if (idx >= 0) {
    //   const card = this.cards[idx];
    //   card.angle = -2;
    //   card.scale.set(1.05);
    //   card.uiUpdateHover(true);
    // }
    this.selectCardId = idx;
  }
}
