import { Canvas } from "../2d/pixi/Canvas";
import { LoadingAnimation, Scene, SceneRoot } from "./Scene";

class CompositorCanvas extends Canvas {
  constructor(root: HTMLElement, aspectRatio: number) {
    const h = 720;
    super(root, h * aspectRatio, h, {
      backgroundAlpha: 0,
    });
  }
}

class CompositorSceneRoot implements SceneRoot {
  public readonly canvas: Canvas;
  public readonly parent: HTMLElement;

  public currScene?: Scene;
  private readonly scenes = new Set<Scene>();

  constructor(parent: HTMLElement, aspectRatio: number, zIndex: number) {
    const div = document.createElement("div");
    div.classList.add("compositor-scene");
    div.style.zIndex = zIndex.toString();
    parent.appendChild(div);
    this.parent = div;
    this.canvas = new CompositorCanvas(div, aspectRatio);
  }

  toggle(scene: Scene) {
    if (!this.scenes.has(scene)) {
      scene.mount(this);
      this.scenes.add(scene);
    }
    this.currScene = scene;
    this.scenes.forEach((e) => e.toggleVisibility(e === scene));
  }
}

export class Compositor {
  private readonly root: HTMLElement;

  private readonly bg: CompositorSceneRoot;
  private readonly fg: CompositorSceneRoot;

  constructor(
    public readonly parent: HTMLElement,
    public readonly aspectRatio: number = 1280 / 720
  ) {
    this.root = document.createElement("div");
    this.root.classList.add("compositor-root");
    this.root.addEventListener("wheel", this.onScroll.bind(this));
    parent.appendChild(this.root);

    this.bg = new CompositorSceneRoot(this.root, this.aspectRatio, 0);
    (<any>window).bgCanvas = this.bg.canvas;

    this.fg = new CompositorSceneRoot(this.root, this.aspectRatio, 0);
    (<any>window).fgCanvas = this.fg.canvas;
    this.fg.parent.classList.add("overlay");

    const resize = () => {
      let { clientWidth: width, clientHeight: height } = parent;
      if (width / height > aspectRatio) {
        width = height * aspectRatio;
      }
      if (width / height < aspectRatio) {
        height = width / aspectRatio;
      }
      this.root.style.margin = `-${height / 2}px 0 0 -${width / 2}px`;
      this.root.style.width = `${width}px`;
      this.root.style.height = `${height}px`;
    };
    window.addEventListener("resize", resize);
    resize();

    (<any>window).compositor = this;
  }

  run(scene: Scene) {
    this.bg.toggle(scene);
    scene.run();
  }

  async wait<T>(anim: LoadingAnimation, payload: () => Promise<T>): Promise<T> {
    this.fg.parent.classList.remove("overlay");
    this.fg.toggle(anim);
    const v = await anim.play(payload);
    anim.toggleVisibility(false);
    this.fg.parent.classList.add("overlay");
    return v;
  }

  private onScroll(event: any) {
    if (this.bg.currScene != null) {
      this.bg.currScene.onScroll(event);
    }
  }
}
