import { Layer } from "@pixi/layers";
import { Container } from "pixi.js";

export class Layers {
  private zLayers = new Map<number, Layer>();
  public readonly root = new Container();

  constructor() {
    this.root.sortableChildren = true;
  }

  get(zIndex: number): Layer {
    let layer = this.zLayers.get(zIndex);
    if (layer == null) {
      layer = new Layer();
      layer.zIndex = 1;
      this.root.addChild(layer);
      this.zLayers.set(zIndex, layer);
    }
    return layer;
  }
}
