import { Card } from "../../core/Card";
import { Component } from "./pixi/Component";
import { Spaces } from "../../core/Common";
import { GridComponent } from "./GridComponent";
import { EmptySpaceTexture } from "./EmptySpaceTexture";
import { Texture } from "pixi.js";

interface ICardGridComponentProps {
  card: Card;
  turn: Turn;
  flat: boolean;
}

export class CardGridComponent extends Component<ICardGridComponentProps> {
  layout = {
    width: 8,
    height: 8,
  };

  constructor() {
    super({
      card: Card.fromId(1),
      turn: 1,
      flat: false,
    });

    const parent = this.addContainer();
    parent.scale.set(1 / 40);

    const nrmTiles = new Map([
      [Spaces.EMPTY, EmptySpaceTexture.V52],
      [Spaces.TRIVIAL, Texture.from("assets/player1_trivial_space.png")],
      [Spaces.SPECIAL, Texture.from("assets/player1_special_space.png")],
      [-Spaces.TRIVIAL, Texture.from("assets/player2_trivial_space.png")],
      [-Spaces.SPECIAL, Texture.from("assets/player2_special_space.png")],
    ]);

    // TODO: support flat
    // const flatTiles = new Map([
    //   [Spaces.EMPTY, EmptySpaceTexture.V],
    //   [Spaces.TRIVIAL, Texture.from("assets/pure_yellow.png")],
    //   [Spaces.SPECIAL, Texture.from("assets/pure_orange.png")],
    // ]);

    const bg = this.addComponent(new GridComponent(), {
      parent,
    });
    bg.update({
      tileset: nrmTiles,
    });

    const fn = () => {
      const card = this.props.card.value;
      const turn = this.props.turn.value;
      const spaces = card.spaces.map((x) => x * turn);
      bg.update({ matrix: { spaces, width: 8, height: 8 } });
    };
    this.props.card.onUpdate(fn);
    this.props.turn.onUpdate(fn);
  }
}
