import { Color } from "./pixi/Color";
import { RenderBuffer } from "./pixi/RenderBuffer";
import { Texture, WRAP_MODES } from "pixi.js";
import { ColorPalette } from "./ColorPalette";
import SpFireGlsl from "../shaders/SpFire.glsl";

interface ISpFireTextureProps {
  primary: Color;
  secondary: Color;
  center: Color;
}

export class SpFireTexture extends RenderBuffer<ISpFireTextureProps> {
  layout = {
    width: 104,
    height: 104,
    center: {
      x: 52,
      y: 68,
      r: 15,
    },
  };

  constructor(props: ISpFireTextureProps) {
    super(props);

    const shader = this.addShader(SpFireGlsl, {
      uColorPrimary: Color.BLACK.rgb01,
      uColorSecondary: Color.BLACK.rgb01,
      uAlphaSampler: Texture.from("assets/SpFire_00.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
      uFlameSampler: Texture.from("assets/SpFire_01.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
      uDistortionSampler: Texture.from("assets/SpFire_02.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
    });

    const {
      width,
      height,
      center: { x, y, r },
    } = this.layout;

    this.addSprite({
      width,
      height,
      filters: [shader],
    });

    const circle = this.addSprite({
      anchor: 0.5,
      x,
      y,
      width: r * 2,
      height: r * 2,
      texture: Texture.from("assets/circle.png"),
    });

    this.props.primary.onUpdate(
      (v) => (shader.uniforms.uColorPrimary = v.rgb01)
    );
    this.props.secondary.onUpdate(
      (v) => (shader.uniforms.uColorSecondary = v.rgb01)
    );
    this.props.center.onUpdate((v) => (circle.tint = v.i32));
  }

  private static p1: Texture;
  public static get P1() {
    return (
      this.p1 ||
      (this.p1 = new SpFireTexture(ColorPalette.Player1.fire).texture)
    );
  }

  private static p2: Texture;
  public static get P2() {
    return (
      this.p2 ||
      (this.p2 = new SpFireTexture(ColorPalette.Player2.fire).texture)
    );
  }
}
