import { Filter } from "pixi.js";
import VertexGlsl from "../../shaders/Vertex.glsl";
import CommonGlsl from "../../shaders/Common.glsl";

export class Shader extends Filter {
  private static readonly INSTANCES: Shader[] = [];

  static from(frag: string, uniforms?: any) {
    if (!uniforms) {
      uniforms = {};
    }
    Object.assign(uniforms, {
      iTime: 0,
    });
    const shader = new Shader(VertexGlsl, CommonGlsl + frag, uniforms);
    Shader.INSTANCES.push(shader);
    return shader;
  }

  public static readonly BLACK = Shader.from(`
    void main() {
      float a = texture2D(uSampler, vTextureCoord).a;
      gl_FragColor = vec4(vec3(0.0), a);
    }
  `);

  static {
    let t0: number = null;
    const fn = (t: number) => {
      if (t0 == null) {
        t0 = t;
      }
      t = (t - t0) / 1000;
      Shader.INSTANCES.forEach((shader) => {
        shader.uniforms.iTime = t;
      });
      window.requestAnimationFrame(fn);
    };
    window.requestAnimationFrame(fn);
  }
}
