import "./css/index.less";

import { Compositor } from "./scripts/Compositor";
import { GamePlayScene } from "./scripts/GamePlayScene";
import { InkResetAnimation } from "./scripts/InkResetAnimation";
import { WebfontLoaderPlugin } from "pixi-webfont-loader";
import { Loader } from "pixi.js";

Loader.registerPlugin(WebfontLoaderPlugin);
Loader.shared.add({ name: "Splatoon", url: "assets/fonts/Splatoon1.otf" });

const compositor = new Compositor(document.body);
const inkReset = new InkResetAnimation();
const game = new GamePlayScene();

// const scene = new TestScene();

Loader.shared.onComplete.once(() => {
  // compositor.run(scene);
  compositor.wait(inkReset, async () => {
    game.reset({
      board: 6,
      decks: [
        [33, 159, 92, 25, 30, 52, 65, 50, 66, 64, 53, 58, 28, 74, 69],
        [86, 110, 105, 161, 53, 118, 125, 69, 109, 20, 143, 44, 12, 113, 75],
      ],
    });
    compositor.run(game);
  });
});
Loader.shared.load();
