import { Sprite, Texture } from "pixi.js";
import { CardPlacement } from "../../core/Board";
import { GameController } from "../../core/GameController";
import { BoardComponent } from "../2d/BoardComponent";
import { HandComponent } from "../2d/HandComponent";
import { SpCutInAnimation } from "../2d/SpCutInAnimation";
import { PixiScene } from "./Scene";
import { ColorPalette } from "../2d/ColorPalette";
import { Color } from "../2d/pixi/Color";
import { SzMeterComponent } from "../2d/SzMeterComponent";
import { CardAnimation } from "../2d/CardAnimation";
import { TurnMeterComponent } from "../2d/TurnMeterComponent";
import { EaseFunc } from "../2d/pixi/Ease";

export class GamePlayScene extends PixiScene {
  private readonly board: BoardComponent;
  private readonly hand: HandComponent;
  private readonly szMeter: SzMeterComponent;
  private readonly turnMeter: TurnMeterComponent;
  private readonly overlay: Sprite;
  private readonly spCutInAnim: SpCutInAnimation;
  private readonly card1: CardAnimation;
  private readonly card2: CardAnimation;

  layout = {
    width: 1920,
    height: 1080,
    board: {
      x: 194,
      y: -16,
      width: 768,
      height: 1040,
    },
    szMeter: {
      x: -320,
      y: -5,
      inks: [
        {
          x: -18,
          y: 80,
          width: 360,
          scale: 1,
          angle: -25,
          alpha: 0.8,
          tint: ColorPalette.Player1.primary,
          img: "assets/Ink_04.png",
        },
        {
          x: 42,
          y: -120,
          width: 360,
          scale: -1,
          alpha: 0.8,
          tint: ColorPalette.Player2.primary,
          img: "assets/Ink_02.png",
        },
      ],
    },
    turnMeter: {
      x: -300,
      y: -350,
    },
    hand: {
      x: -683,
      y: -52,
      width: 600,
      height: 660,
    },
    card: {
      x: 772,
      y: -27,
      p1: {
        x: 0,
        y: 300,
      },
      p2: {
        x: 0,
        y: -300,
      },
    },
  };

  private controller: GameController;

  constructor() {
    super({
      bgTint: Color.fromHex(0x4838a3),
    });

    // const bgShader = this.addShader(BlendGlsl, {
    //   uColorPrimary: ColorPalette.GamePlay.bg.primary.rgb01,
    //   uColorSecondary: ColorPalette.GamePlay.bg.secondary.rgb01,
    // });

    // this.bg.texture = Texture.from("assets/IngameBG.png");
    // this.bg.filters = [bgShader];

    // const { state } = this.game;

    const root = this.addContainer({
      x: this.layout.width / 2,
      y: this.layout.height / 2,
    });

    this.board = this.addComponent(new BoardComponent(), {
      parent: root,
      anchor: 0.5,
      x: this.layout.board.x,
      y: this.layout.board.y,
      scale: {
        width: this.layout.board.width,
        height: this.layout.board.height,
      },
    });
    this.board.onPlayerInput((e, ok) => {
      if (!ok) {
        this.szMeter.update({
          preview: false,
        });
      } else {
        const { board } = this.controller.state;
        const { spaceCount1, spaceCount2 } = board.move([e]);
        this.szMeter.update({
          preview: true,
          preview1: spaceCount1,
          preview2: spaceCount2,
        });
      }
    });

    this.szMeter = this.addComponent(new SzMeterComponent(), {
      parent: root,
      x: this.layout.szMeter.x,
      y: this.layout.szMeter.y,
    });

    this.hand = this.addComponent(new HandComponent(), {
      parent: root,
      anchor: 0.5,
      x: this.layout.hand.x,
      y: this.layout.hand.y,
      scale: {
        width: this.layout.hand.width,
        height: this.layout.hand.height,
      },
    });
    this.hand.onSelectCard(this.onSelectCard.bind(this));

    const cardRoot = this.addContainer({
      parent: root,
      x: this.layout.card.x,
      y: this.layout.card.y,
    });
    cardRoot.scale.set(0.85);
    [this.card1, this.card2] = [this.layout.card.p1, this.layout.card.p2].map(
      (p) =>
        this.addComponent(new CardAnimation(), {
          parent: cardRoot,
          x: p.x,
          y: p.y,
        })
    );
    this.card2.update({ turn: -1 });

    this.overlay = this.addSprite({
      parent: root,
      anchor: 0.5,
      width: this.layout.width,
      height: this.layout.height,
      tint: Color.BLACK,
      texture: Texture.WHITE,
      alpha: 0,
    });

    this.turnMeter = this.addComponent(new TurnMeterComponent(), {
      parent: root,
      anchor: 0.5,
      x: this.layout.turnMeter.x,
      y: this.layout.turnMeter.y,
    });

    // const btn = new ButtonComponent({ width: 160, height: 50, radius: 10 });
    // btn.position.set(20, 560);
    // this.addChild(btn);

    // const spMeter = new SpMeterComponent(1);
    // spMeter.scaleToFit(20);
    // spMeter.position.set(38, 654);
    // spMeter.uiUpdate(2);
    // this.addChild(spMeter);

    this.spCutInAnim = new SpCutInAnimation();
    this.spCutInAnim.scaleToFit(this.layout.width, this.layout.height);
    this.addChild(this.spCutInAnim);
  }

  reset(record: GameRecord) {
    this.controller = GameController.fromGameRecord(record);
    const { state } = this.controller;
    this.board.uiReset(state.board);
    this.szMeter.update({
      value1: state.board.spaceCount1,
      value2: state.board.spaceCount2,
    });
  }

  async run() {
    const { state } = this.controller;
    this.hand.update({ cards: state.player1.hand.slice() });
    this.overlay.visible = false;

    await this.turnMeter.update({ value: 12 });

    for (let i = 12; i > 0; --i) {
      const [m1, m2] = await this.getInputs();

      const { state: prevState } = this.controller;
      const state = this.controller.move(m1, m2);

      const sleep = async (t: number) => this.addAnimation().play(t);
      const dt = 0.75;

      this.szMeter.update({
        preview: false,
      });

      await this.spCutInAnim.uiPlay(state.player1.card, state.player2.card);

      this.hand.uiResetSelection();

      await Promise.all([
        this.card1.uiShowCard(state.player1.card),
        this.card2.uiShowCard(state.player2.card),
      ]);

      for (const li of state.cardPlacements) {
        await sleep(dt);
        await this.board.uiPlaceCards(li);
      }

      if (
        state.board.specialPoints1 != prevState.board.specialPoints1 ||
        state.board.specialPoints2 != prevState.board.specialPoints2
      ) {
        await sleep(dt);
        this.board.uiUpdateFire();
      }

      await sleep(dt);
      await this.szMeter.uiUpdate(
        state.board.spaceCount1,
        state.board.spaceCount2
      );

      await Promise.all([
        this.hand.uiDrawCard(state.player1.hand[m1.handId], m1.handId),
        this.card1.uiHideCard(),
        this.card2.uiHideCard(),
      ]);

      const alpha = 0.5;
      const a1 = this.addAnimation((t) => {
        this.overlay.alpha = EaseFunc.LINEAR.interpolate(0, alpha, t);
      });
      const a2 = this.addAnimation((t) => {
        this.overlay.alpha = EaseFunc.LINEAR.interpolate(alpha, 0, t);
      });

      this.overlay.visible = true;
      await Promise.all([
        a1
          .play(0.2)
          .then(() => this.addAnimation().play(1))
          .then(() => a2.play(0.2)),
        this.turnMeter.uiUpdate(i - 1),
      ]);
      this.overlay.visible = false;
    }
  }

  getInputs(): Promise<PlayerMovement[]> {
    const p1 = this.getValidPlayerInput();
    const p2 = this.getRivalInput();
    return Promise.all([p1, p2]);
  }

  async getRivalInput(): Promise<PlayerMovement> {
    await this.addAnimation().play(0.5);
    const m: PlayerMovement = {
      type: "discard",
      handId: 0,
    };

    const { state } = this.controller;
    await this.card2.uiSelectCard(state.player2.hand[m.handId]);

    return m;
  }

  async getValidPlayerInput(): Promise<PlayerMovement> {
    let move: PlayerMovement;
    this.board.update({
      input: {
        card: null,
        rotation: 0,
        pointer: null,
        isSpecialAttack: false,
      },
    });

    const { state } = this.controller;
    do {
      move = await this.getPlayerInput();
      if (state.isValidMovement(1, move)) {
        break;
      }
      console.log("invalid movement:", move);
    } while (1);
    console.log(move);

    const card = state.player1.hand[move.handId];
    this.board.update({
      input: null,
    });
    await this.card1.uiSelectCard(card);

    return move;
  }

  async getPlayerInput(): Promise<PlayerMovement> {
    // TODO: Promise.race();
    const input: CardPlacement = await this.board.receive("player.input");
    const { handId, isSpecialAttack } = <any>this.board.props.input.value;
    const { rotation, position } = input;
    return {
      type: isSpecialAttack ? "special" : "trivial",
      handId,
      params: {
        rotation,
        position,
      },
    };
  }

  onSelectCard(handId: number) {
    const input = this.board.props.input.value;
    if (input == null) {
      return;
    }
    const card = this.controller.state.player1.hand[handId];
    this.board.update({
      input: Object.assign({}, input, {
        handId,
        card,
      }),
    });
    this.card1.update({ card });
  }

  override onScroll(event: any) {
    const input = this.board.props.input.value;
    if (input == null) {
      return;
    }
    const x = input.rotation + event.deltaY / 100;
    const rotation = <Rotation>(((x % 4) + 4) % 4);
    this.board.update({
      input: Object.assign({}, input, {
        rotation,
      }),
    });
  }
}
