import {
  Container,
  DisplayObject,
  Filter,
  Graphics,
  ObservablePoint,
  Sprite,
  Text,
  ITextStyle,
  Texture,
} from "pixi.js";
import { Cell } from "./Cell";
import { Layers } from "./Layers";
import { Layer } from "@pixi/layers";
import { Shader } from "./Shader";
import { Animation, IAnimationOptions, KeyFrameFuncType } from "./Animation";
import { Color } from "./Color";

function isEqual(a: any, b: any): boolean {
  if (a == b) {
    return true;
  }
  if (Array.isArray(a)) {
    return (
      Array.isArray(b) &&
      a.length == b.length &&
      a.every((x, i) => isEqual(x, b[i]))
    );
  }
  return false;
}

interface IPipe {
  promise: Promise<any>;
  resolve: any;
  reject: any;
}

interface IDisplayObjectOptions {
  parent?: Container;
  x?: number;
  y?: number;
  angle?: number;
  filters?: Filter[];
  layer?: Layer;
  zIndex?: number;
}

interface ISpriteOptions extends IDisplayObjectOptions {
  width?: number;
  height?: number;
  texture?: Texture;
  tint?: Color;
  alpha?: number;
  scale?:
    | {
        x: number;
        y?: number;
      }
    | number;
  anchor?:
    | {
        x: number;
        y?: number;
      }
    | number;
}

interface ITextOptions extends ISpriteOptions {
  text?: string;
  style?: Partial<ITextStyle>;
}

interface IComponentOptions {
  parent?: Container;
  x?: number;
  y?: number;
  scale?: {
    width: number;
    height: number;
  };
  anchor?:
    | {
        x: number;
        y?: number;
      }
    | number;
}

interface IShadowOptions {
  x: number;
  y: number;
  alpha: number;
}

export class Component<IProps extends {} = {}> {
  public readonly anchor = new ObservablePoint(this.onUpdateAnchor, this);
  public readonly ui = new Container();

  protected readonly orig = new Container();
  private readonly ui1 = new Container();
  private readonly ui2 = new Container();

  private readonly pipes = new Map<string, IPipe[]>();

  public layout: {
    width: number;
    height: number;
  };

  public readonly props: { [P in keyof IProps]: Cell<IProps[P]> } = <any>{};

  constructor(props: IProps = <any>{}) {
    this.ui.addChild(this.ui1);
    this.ui1.addChild(this.ui2);

    for (const key in props) {
      const cell = Cell.of(props[key]);
      this.props[key] = cell;
      setTimeout(() => (<any>cell).mUpdateFunc(cell.value), 0);
    }
  }

  update(newProps: { [P in keyof IProps]?: IProps[P] }): Promise<any> {
    const li = [];
    for (const key in newProps) {
      const prop = this.props[key];
      if (prop == null) {
        continue;
      }
      const v = newProps[key];
      if (!isEqual(prop.value, v)) {
        li.push(prop.update(v));
      }
    }
    return Promise.all(li);
  }

  addShader(frag: string, uniforms?: any): Shader {
    return Shader.from(frag, uniforms);
  }

  addAnimation(options?: IAnimationOptions | KeyFrameFuncType): Animation {
    return new Animation(options || {});
  }

  addChild(e: Component | DisplayObject) {
    if (e instanceof Component) {
      this.ui2.addChild(e.ui);
      this.orig.addChild(e.orig);
    } else {
      this.ui2.addChild(e);
    }
  }

  addLayers(): Layers {
    const layers = new Layers();
    this.addChild(layers.root);
    return layers;
  }

  addContainer(options?: IDisplayObjectOptions): Container {
    const obj = new Container();
    this.doAddDisplayObject(obj, options);
    return obj;
  }

  addGraphics(options?: IDisplayObjectOptions): Graphics {
    const obj = new Graphics();
    this.doAddDisplayObject(obj, options);
    return obj;
  }

  addSprite(options?: ISpriteOptions): Sprite {
    const obj = new Sprite();
    this.doAddSprite(obj, options);
    return obj;
  }

  addText(options: ITextOptions): Text {
    const obj = new Text();
    const { text, style } = options || {};
    if (style != null) {
      obj.style = style;
    }
    if (text != null) {
      obj.text = text;
    }
    this.doAddSprite(obj, options);
    return obj;
  }

  addComponent<U extends Component>(obj: U, options?: IComponentOptions): U {
    const { parent, x, y, scale, anchor } = options || {};
    obj.position.set(x || 0, y || 0);
    if (scale != null) {
      obj.scaleToFit(scale.width, scale.height);
    }
    if (anchor != null) {
      if (typeof anchor == "number") {
        obj.anchor.set(anchor);
      } else {
        obj.anchor.set(anchor.x, anchor.y);
      }
    }
    (parent || this.ui2).addChild(obj.ui);
    this.orig.addChild(obj.orig);
    return obj;
  }

  castShadow(obj: DisplayObject | Component, options: IShadowOptions) {
    const layers = this.addLayers();
    if (obj instanceof Component) {
      obj = obj.ui;
    }
    const l0 = layers.get(0);
    l0.useRenderTexture = true;
    obj.parentLayer = l0;

    const texture = l0.getRenderTexture();
    const l1 = layers.get(1);

    const { x, y, alpha } = options || {};
    const s0 = new Sprite(texture);
    s0.parentLayer = l1;
    s0.x = x;
    s0.y = y;
    s0.filters = [Shader.BLACK];
    s0.alpha = alpha;
    this.orig.addChild(s0);

    const s1 = new Sprite(texture);
    s1.parentLayer = l1;
    this.orig.addChild(s1);
  }

  private doAddSprite(obj: Sprite, options?: ISpriteOptions) {
    const { width, height, texture, anchor, tint, scale, alpha } =
      options || {};
    if (width != null) {
      obj.width = width;
    }
    if (height != null) {
      obj.height = height;
    }
    if (scale != null) {
      if (typeof scale == "number") {
        obj.scale.set(scale);
      } else {
        obj.scale.set(scale.x, scale.y);
      }
    }
    if (anchor != null) {
      if (typeof anchor == "number") {
        obj.anchor.set(anchor);
      } else {
        obj.anchor.set(anchor.x, anchor.y);
      }
    }
    if (texture != null) {
      obj.texture = texture;
    }
    if (alpha != null) {
      obj.alpha = alpha;
    }
    if (tint != null) {
      obj.tint = tint.i32;
    }
    this.doAddDisplayObject(obj, options);
  }

  private doAddDisplayObject(
    obj: DisplayObject,
    options?: IDisplayObjectOptions
  ) {
    const { parent, x, y, angle, filters, layer, zIndex } = options || {};
    obj.position.set(x || 0, y || 0);
    if (angle != null) {
      obj.angle = angle;
    }
    if (filters != null) {
      obj.filters = filters.slice();
    }
    if (layer != null) {
      obj.parentLayer = layer;
    }
    if (zIndex != null) {
      obj.zIndex = zIndex;
    }
    (parent || this.ui2).addChild(obj);
  }

  scaleToFit(width: number, height?: number) {
    height = height || width;
    const scale = Math.min(
      width / this.layout.width,
      height / this.layout.height
    );
    this.ui1.scale.set(scale);
  }

  get position() {
    return this.ui.position;
  }

  get scale() {
    return this.ui.scale;
  }

  get angle() {
    return this.ui.angle;
  }

  set angle(value: number) {
    this.ui.angle = value;
  }

  get visible() {
    return this.ui.visible;
  }

  set visible(value: boolean) {
    this.ui.visible = value;
  }

  private onUpdateAnchor() {
    this.ui2.position.set(
      -this.anchor.x * this.layout.width,
      -this.anchor.y * this.layout.height
    );
  }

  receive(topic: string): Promise<any> {
    let resolve, reject;
    const promise = new Promise((x, y) => {
      resolve = x;
      reject = y;
    });
    const pipe = {
      promise,
      resolve,
      reject,
    };
    if (this.pipes.has(topic)) {
      this.pipes.get(topic).push(pipe);
    } else {
      this.pipes.set(topic, [pipe]);
    }
    return promise;
  }

  send(topic: string, value: any): boolean {
    if (!this.pipes.has(topic)) {
      return false;
    }
    this.pipes.get(topic).forEach(({ resolve }) => resolve(value));
    this.pipes.delete(topic);
    return true;
  }
}
